#box{
    text-align: center;
    border: 1px solid green;
    border-bottom-right-radius: 20px;
    border-top-left-radius: 20px;
}

#box:hover{
    transform: translateY(-10px) scale(1.05);
    box-shadow: 0 0 20px black;
    transition: all 1s ease-in-out;
}

#box h3{
    color: green;
}

/* Loader home */
#loader{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}